import { useEffect, useRef, useState, useCallback } from "react";
import mapboxgl from "mapbox-gl";
import "mapbox-gl/dist/mapbox-gl.css";
import { Restaurant } from "@/hooks/useRestaurants";
import { calculateDistance } from "@/lib/geolocation";
import { useTranslation } from "react-i18next";
import { isIOSDevice, isIOSWebView } from "@/lib/externalLinks";
import { AlertTriangle, MapPin } from "lucide-react";
import { Button } from "@/components/ui/button";
import mapIcon from "@/assets/map-icon.png";

interface RestaurantMapProps {
  restaurants: Restaurant[];
  onRestaurantClick: (restaurant: Restaurant) => void;
  userLocation: { lat: number; lng: number } | null;
  radiusFilter: number | null;
  onMapInit?: (map: mapboxgl.Map) => void;
  onMarkerClick?: (restaurant: Restaurant) => void;
  showViewDetailsButton?: boolean;
}

const RestaurantMap = ({ 
  restaurants, 
  onRestaurantClick, 
  userLocation,
  radiusFilter,
  onMapInit,
  onMarkerClick,
  showViewDetailsButton = false
}: RestaurantMapProps) => {
  const { i18n, t } = useTranslation();
  const mapContainer = useRef<HTMLDivElement>(null);
  const map = useRef<mapboxgl.Map | null>(null);
  const markers = useRef<mapboxgl.Marker[]>([]);
  const [mapLoaded, setMapLoaded] = useState(false);
  const [mapError, setMapError] = useState<string | null>(null);
  const currentPopup = useRef<mapboxgl.Popup | null>(null);

  useEffect(() => {
    if (!mapContainer.current || map.current) return;

    const token = import.meta.env.VITE_MAPBOX_TOKEN;
    if (!token) {
      console.warn('[RestaurantMap] No Mapbox token found');
      return;
    }

    // Check WebGL support
    if (!mapboxgl.supported()) {
      console.warn('[RestaurantMap] WebGL not supported', {
        isIOSDevice: isIOSDevice(),
        isIOSWebView: isIOSWebView(),
        userAgent: navigator.userAgent
      });
      setMapError(t('map.webglNotSupported', 'Your browser does not support map display'));
      return;
    }

    // Wait for container to have valid dimensions
    const initMap = () => {
      if (!mapContainer.current) return;
      
      const rect = mapContainer.current.getBoundingClientRect();
      console.log('[RestaurantMap] Container dimensions:', rect.width, rect.height);
      
      if (rect.width === 0 || rect.height === 0) {
        setTimeout(initMap, 100);
        return;
      }

      try {
        mapboxgl.accessToken = token;

        const initialCenter: [number, number] = userLocation 
          ? [userLocation.lng, userLocation.lat]
          : [-9.1393, 38.7223];

        map.current = new mapboxgl.Map({
          container: mapContainer.current,
          style: "mapbox://styles/mapbox/light-v11",
          center: initialCenter,
          zoom: 13,
          cooperativeGestures: true,
          doubleClickZoom: false,
        });

        map.current.on('error', (e) => {
          console.error('[RestaurantMap] Map error:', e);
          setMapError(t('map.loadError', 'Map failed to load'));
        });

        map.current.addControl(
          new mapboxgl.NavigationControl({
            visualizePitch: true,
          }),
          "top-right"
        );

        map.current.on("load", () => {
          setMapLoaded(true);
          
          if (onMapInit && map.current) {
            onMapInit(map.current);
          }
        });
      } catch (error) {
        console.error('[RestaurantMap] Failed to initialize map:', error);
        setMapError(t('map.loadError', 'Map failed to load'));
      }
    };

    initMap();

    return () => {
      markers.current.forEach(marker => marker.remove());
      markers.current = [];
      if (currentPopup.current) {
        currentPopup.current.remove();
        currentPopup.current = null;
      }
      map.current?.remove();
      map.current = null;
      setMapLoaded(false);
    };
  }, []);

  // Update map center when user location changes
  useEffect(() => {
    if (map.current && userLocation && mapLoaded) {
      map.current.flyTo({
        center: [userLocation.lng, userLocation.lat],
        zoom: 14,
        duration: 1500,
      });
    }
  }, [userLocation, mapLoaded]);

  useEffect(() => {
    if (!map.current || !mapLoaded) return;

    markers.current.forEach(marker => marker.remove());
    markers.current = [];
    if (currentPopup.current) {
      currentPopup.current.remove();
      currentPopup.current = null;
    }

    let filteredRestaurants = restaurants;
    if (userLocation && radiusFilter) {
      filteredRestaurants = restaurants.filter(restaurant => {
        const distance = calculateDistance(
          userLocation.lat,
          userLocation.lng,
          restaurant.lat,
          restaurant.lng
        );
        return distance <= radiusFilter;
      });
    }

    filteredRestaurants.forEach((restaurant) => {
      const popupContent = document.createElement('div');
      popupContent.className = 'p-3 min-w-[200px]';
      popupContent.innerHTML = `
        <div class="space-y-2">
          <h3 class="font-bold text-base text-foreground">${restaurant.name}</h3>
          ${restaurant.type && restaurant.type.length > 0 ? `
            <div class="flex flex-wrap gap-1">
              ${restaurant.type.slice(0, 2).map(type => 
                `<span class="text-xs bg-secondary text-secondary-foreground px-2 py-0.5 rounded">${type}</span>`
              ).join('')}
            </div>
          ` : ''}
          ${restaurant.rating ? `
            <div class="flex items-center gap-1">
              <span style="color: #016450;">‚≠ê</span>
              <span class="font-semibold text-sm">${restaurant.rating.toFixed(1)}</span>
            </div>
          ` : ''}
          <p class="text-xs text-muted-foreground">${restaurant.address}</p>
          ${showViewDetailsButton ? `
            <button 
              id="view-details-${restaurant.id}" 
              class="w-full mt-2 px-3 py-1.5 text-sm font-medium text-white rounded-md transition-colors"
              style="background-color: #016450;"
              onmouseover="this.style.opacity='0.9'"
              onmouseout="this.style.opacity='1'"
            >
              ${t('restaurants.viewDetails')}
            </button>
          ` : ''}
        </div>
      `;

      const popup = new mapboxgl.Popup({ 
        offset: 25,
        closeButton: true,
        closeOnClick: false,
        closeOnMove: false,
        maxWidth: '300px'
      })
        .setLngLat([restaurant.lng, restaurant.lat])
        .setDOMContent(popupContent);

      const el = document.createElement('div');
      el.className = 'custom-marker';
      el.style.backgroundImage = `url(${mapIcon})`;
      el.style.width = '40px';
      el.style.height = '40px';
      el.style.backgroundSize = 'contain';
      el.style.backgroundRepeat = 'no-repeat';
      el.style.backgroundPosition = 'center';
      el.style.cursor = 'pointer';

      const marker = new mapboxgl.Marker({ 
        element: el,
        draggable: false
      })
        .setLngLat([restaurant.lng, restaurant.lat])
        .addTo(map.current!);

      const markerElement = marker.getElement();
      markerElement.style.cursor = 'pointer';
      
      // Remove any existing click handlers
      const handleMarkerClick = (e: MouseEvent) => {
        e.stopPropagation();
        e.preventDefault();
        
        if (currentPopup.current && currentPopup.current !== popup) {
          currentPopup.current.remove();
        }
        
        popup.addTo(map.current!);
        currentPopup.current = popup;
        
        map.current!.easeTo({
          center: [restaurant.lng, restaurant.lat],
          zoom: 17,
          duration: 800,
        });
        
        if (onMarkerClick) {
          onMarkerClick(restaurant);
        }
      };
      
      markerElement.addEventListener('click', handleMarkerClick, true);

      if (showViewDetailsButton) {
        popup.on('open', () => {
          const button = document.getElementById(`view-details-${restaurant.id}`);
          if (button) {
            button.addEventListener('click', (e) => {
              e.stopPropagation();
              onRestaurantClick(restaurant);
            });
          }
        });
      }

      popup.on('close', () => {
        if (currentPopup.current === popup) {
          currentPopup.current = null;
        }
      });

      markers.current.push(marker);
    });

    if (filteredRestaurants.length > 0) {
      const bounds = new mapboxgl.LngLatBounds();
      filteredRestaurants.forEach(restaurant => {
        bounds.extend([restaurant.lng, restaurant.lat]);
      });
      
      if (userLocation) {
        bounds.extend([userLocation.lng, userLocation.lat]);
      }

      map.current.fitBounds(bounds, {
        padding: 50,
        maxZoom: 15,
      });
    }
  }, [restaurants, radiusFilter, userLocation, mapLoaded, onRestaurantClick, i18n.language, showViewDetailsButton, t, onMarkerClick]);

  useEffect(() => {
    if (!map.current || !userLocation || !mapLoaded) return;

    const userMarkerEl = document.createElement("div");
    userMarkerEl.className = "user-location-marker";
    userMarkerEl.style.backgroundColor = "#3b82f6";
    userMarkerEl.style.width = "20px";
    userMarkerEl.style.height = "20px";
    userMarkerEl.style.borderRadius = "50%";
    userMarkerEl.style.border = "3px solid white";
    userMarkerEl.style.boxShadow = "0 2px 4px rgba(0,0,0,0.3)";

    const userMarker = new mapboxgl.Marker(userMarkerEl)
      .setLngLat([userLocation.lng, userLocation.lat])
      .addTo(map.current);

    return () => {
      userMarker.remove();
    };
  }, [userLocation, mapLoaded]);

  // Show error fallback UI
  if (mapError) {
    return (
      <div className="w-full h-full rounded-lg overflow-hidden bg-muted/50 flex flex-col items-center justify-center p-6 text-center">
        <AlertTriangle className="w-12 h-12 text-muted-foreground mb-4" />
        <p className="text-muted-foreground mb-2">{mapError}</p>
        <p className="text-sm text-muted-foreground mb-4">
          {t('map.viewListInstead', 'Please view the restaurant list below')}
        </p>
        {isIOSWebView() && (
          <Button
            variant="outline"
            size="sm"
            onClick={() => window.open(window.location.href, '_blank')}
          >
            <MapPin className="w-4 h-4 mr-2" />
            {t('map.openInBrowser', 'Open in Browser')}
          </Button>
        )}
      </div>
    );
  }

  return (
    <div ref={mapContainer} className="w-full h-full rounded-lg overflow-hidden" />
  );
};

export default RestaurantMap;
